<template>
  <section class="h100">
    <div class="container full-page" v-if="!authenticated">
      <h4 class="title is-4">Admin Panel</h4>
      <form class="display-black" @submit.prevent="onProceed">
        <div
          class="admin"
          v-if="user && user.id == '1knQIaLmRpVb0BDpomw5Kcz0rsW2'"
        >
          <p>Welcome, {{ user.name }}</p>
        </div>
        <div v-if="user.id != '1knQIaLmRpVb0BDpomw5Kcz0rsW2'">
          <label for="email"><small>Email:</small></label>
          <input class="input" id="email" type="email" placeholder="Email" />
        </div>
        <label for="key"><small>Auth key:</small></label>
        <p class="control has-icons-left">
          <input
            class="input"
            id="key"
            type="password"
            placeholder="key"
            v-model="userData.key"
          />
          <span class="icon is-small is-left">
            <i class="fa fa-lock"></i>
          </span>
        </p>
        <small v-if="errorMsg" style="color: red">{{ errorMsg }}</small>
        <button class="button proceed">Proceed</button>
      </form>
    </div>

    <div class="admin-panel container" v-if="authenticated && user">
      <h1 class="title">Administrative Panel</h1>
      <p class="pending-heading">Pending posts</p>
      <p v-if="posts.length == 0" style="text-align: center">No pending posts</p>
      <div class="posts">
     <div class="card is-4" v-for="(post, index) in posts.slice().reverse()"
      :key="post.id">
        <div class="card-image" v-if="post.URL">
          <figure class="posted-image">
            <img :src="post.URL" alt="Placeholder image" />
          </figure>
        </div><!--card-image-->
        <div class="card-content">
          <div class="media">
            <div class="media-left">
              <figure class="image is-48x48">
                <img :src="loadedUsersById[post.user_id].image" alt="Avatar" />
              </figure>
            </div><!--media-left-->
            <div class="media_content">
              <p class="title is-4">{{ post.title }}</p>
              <p class="subtitle is-6">
                {{ loadedUsersById[post.user_id].name }}
                <br />
                <time>{{ getCreated(index) }}</time>
              </p>
            </div><!--media-content-->
          </div><!--media-->

          <pre class="content">
            {{ post.description }}
            <br />
          </pre><!--content-->
        </div><!--card-content-->
        <div class="view">
          <router-link
            :to="{
              name: 'post',
              params: {
                name: 'pending',
                post_id: post.id
              }
            }"
            class="button view-post submit"
            >View Post</router-link
          >
          <span class="like-icon" v-bind:class="{ 'danger': post.vote.includes(user.id) }">
            <b-icon pack="fa" icon="thumbs-up" size="is-medium"> </b-icon>
            <small>{{post.vote.length}}</small>
          </span>
          <span class="like-icon">
            <b-icon pack="fa" icon="comment" size="is-medium"> </b-icon>
            <small>{{post.comment_no}}</small>
          </span>
        <button class="button is-success" @click="onApprovePost(post.id)"
        v-if="user && user.id=='1knQIaLmRpVb0BDpomw5Kcz0rsW2'"> Approve</button>
        </div><!--view-->
      </div> <!--loop-->
    </div>

    <div class="users-lis">
      <h1 class="title is-5">Users</h1>
      <div class="all-users" v-if="users">
          <ol type="1">
            <li v-for="user in users" :key="user.id">
              <img class="user-img" :src="user.image" :alt="user.name" ><b>{{user.name}}</b><br/>
              ID: {{user.id}} <br>
              Logged in at: {{new Date(user.created_at.seconds*1000)}}
              <hr/>
            </li>
          </ol>
      </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex';

export default {
  data() {
    return {
      authenticated: false,
      errorMsg: '',
      userData: {
        user_id: '',
        key: '',
      },
    };
  },
  mounted() {
    this.initUsers();
    this.initAdmin();
    this.initPendingPost();
  },
  watch: {
    // eslint-disable-next-line space-before-function-paren
    // eslint-disable-next-line object-shorthand
    '$route.params.name'() {
    },
  },
  computed: {
    ...mapState('auth', ['user', 'isLoggedIn']),
    ...mapState('users', ['users']),
    ...mapState('admin', ['admin', 'posts']),
    ...mapGetters({
      usersById: 'users/usersById',
    }),
    loadedUsersById() {
      return this.posts.reduce((byId, post) => {
      // eslint-disable-next-line no-param-reassign
        byId[post.user_id] = this.usersById[post.user_id] || {
          name: 'Loading...',
          image: 'https://bulma.io/images/placeholders/48x48.png',
        };
        return byId;
      }, {});
    },
  },
  methods: {
    ...mapActions('admin', ['initAdmin', 'initPendingPost', 'approvePost']),
    ...mapActions('users', { initUsers: 'init' }),
    onProceed() {
      if (this.userData.key) {
        this.userData.user_id = this.user.id;
        if (
          this.userData.key === this.admin[0].key
          && this.userData.user_id === this.admin[0].user_id
        ) {
          this.authenticated = true;
        } else {
          this.errorMsg = 'Wrong authentication key';
        }
      }
    },
    getCreated(index) {
      function timeSince(date) {
        const seconds = Math.floor((new Date() - date) / 1000);
        let interval = Math.floor(seconds / 31536000);
        if (interval > 1) {
          return `${interval} years`;
        }
        interval = Math.floor(seconds / 2592000);
        if (interval > 1) {
          return `${interval} months`;
        }
        interval = Math.floor(seconds / 86400);
        if (interval > 1) {
          return `${interval} days`;
        }
        interval = Math.floor(seconds / 3600);
        if (interval > 1) {
          return `${interval} hours`;
        }
        interval = Math.floor(seconds / 60);
        if (interval > 1) {
          return `${interval} minutes`;
        }
        return `${Math.floor(seconds || 0)} seconds`;
      }
      const posts = this.posts.slice().reverse();
      return timeSince(posts[index].created_at.seconds * 1000) < 0
        ? '0 seconds ago'
        : `${timeSince(posts[index].created_at.seconds * 1000)} ago`;
    },
    onApprovePost(id) {
      this.approvePost(id);
    },
  },
};
</script>

<style>
.user-img{
  height: 70px;
  border: 3px solid black;
  margin: 0 10px 0 0;
}
.all-users ol{    margin: 0 0 0 65px;}
.h100{height: 85%;}
.admin-panel{width: 100%;}
.pending-heading{padding: 0px 10px; background-color:wheat;font-weight: bold;}
.admin-panel h1{padding: 10px; background-color:beige;}
.proceed {
  margin: 10px auto;
  width: 100%;
  font-weight: bold;
}
.full-page {
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.full-page form {
  border: 1px solid;
  padding: 10px;
  border-radius: 5px;
}
.display-black {
  background: black;
  color: white;
}
html,
body,
#app {
  height: 100%;
}
.admin p {
  font-weight: bolder;
}
</style>
