import { firestoreAction } from 'vuexfire';
import db from '@/db';

const state = {
  blogs: [],
  posts: [],
};

const actions = {
  init: firestoreAction(({ bindFirestoreRef }) => {
    bindFirestoreRef('blogs', db.collection('blogs').orderBy('created_at'));
  }),
  showAllPosts: firestoreAction(({ bindFirestoreRef }, number = 0) => {
    bindFirestoreRef('posts', db.collection('posts')
      .where('approval', '==', true).orderBy('created_at')
      .limit(11 + number));
  }),
};

export default {
  namespaced: true,
  state,
  actions,
};
