<template>
  <section class="post-container">
    <div class="full_post" v-if="post[0]">

      <div class="options">
        <button class="button is-danger" v-if="user && user.id == post[0].user_id"
         @click="clickedEdit">Edit</button>
        <button class="button is-dark" v-if="user && (user.id == post[0].user_id
        || user.id=='1knQIaLmRpVb0BDpomw5Kcz0rsW2')"
         @click="displayDeleteConfirm = !displayDeleteConfirm">
          Delete
        </button>
      </div>

      <div class="edit-post" v-if="ShowEdit && isLoggedIn">
        <form @submit.prevent="onEditPost(updatedPost)">
      <b-field label="Title:">
        <b-input placeholder="Title"
        v-model="updatedPost.title" required></b-input>
      </b-field>
      <b-field label="Description:">
        <b-input type="textarea" placeholder="Description"
        v-model="updatedPost.description" required>
        </b-input>
      </b-field>
      <b-field label="If your post includes any codes, please paste them here. (Optional)">
        <b-input v-model="updatedPost.codes" type="textarea">
        </b-input>
      </b-field>
      <button class="button submit">Submit</button>
      <span class="button cancel-edit is-dark" @click="confirmCancel = true">Cancel</span>
    </form>

    <div class="block fixed delete-confirm" v-if="confirmCancel">
      <h1 class="title is-6">Cancel editing?</h1>
      <button class="button yes" @click="cancelEdit">Yes</button>
      <button class="button is-dark" @click="confirmCancel = false">
        No
      </button>
    </div>
      </div>

      <div v-if="displayDeleteConfirm" class="fixed delete-confirm">
        <h1 class="title is-4">Are you sure you want to delete your post?</h1>
        <div>
          <button class="button yes" @click="onDeletePost(post[0].id)">Yes</button>
          <button class="button is-dark" @click="displayDeleteConfirm = !displayDeleteConfirm">
            No
          </button>
        </div>
      </div>

      <h1 class="title text-center">{{ post[0].title }}</h1>
      <div class="card-image" v-if="post[0].URL">
        <figure class="posted-image">
          <img class="full-img" :src="post[0].URL" alt="ccitc blog-post image" />
        </figure>
      </div>

      <pre class="content" v-html="linkExtract(post[0].description.trim())">
        vvvoo
      </pre>
      <pre class="content justify" v-if="post[0].links">
        <a :href="post[0].links" target="_blank" rel="noopener noreferrer">
          {{post[0].links}}</a>
      </pre>
      <div class="posted-codes" v-if="post[0].codes">
        <pre>{{ post[0].codes }}</pre>
      </div>
      <div class="flex posted-by">
        <span>Posted by: </span>
        <figure class="image is-48x48">
          <img :src="loadedUsersById[post[0].user_id].image" alt="Avatar" />
        </figure>
        <p>{{ loadedUsersById[post[0].user_id].name }}</p>
      </div>
      <div class="flex posted-at">
        <time>{{ getCreated(post[0].created_at.seconds) }}</time>
      </div>
      <hr />
        <div class="react">
          <div>
          <button
            class="button upvote"
            v-bind:class="{ 'is-danger': votes }"
            @click="onUpvote()"
            :disabled="!isLoggedIn"
          >
            <b-icon pack="fa" icon="thumbs-up" size="is-medium"></b-icon>
          </button>
          <span>{{ post[0].vote.length }}</span>
          </div>
          <div>
            <b-icon pack="fa" icon="comment" size="is-medium"> </b-icon>
          <span>{{ post[0].comment_no }}</span>
          </div>
        </div>
        <form class="comment" @submit.prevent="onComment(comment)">
          <textarea v-model="comment.content" name="comment" placeholder="Write comment">
          </textarea>
          <button class="button is-dark" :disabled="!isLoggedIn">
            <b-icon pack="fa" icon="location-arrow" size="is-medium"> </b-icon>
          </button>
        </form>
    </div>
    <div class="comment-section" v-if="comments">
      <pre class="_comment" v-for="comment in comments" :key="comment.id">
        <small class="commenter">{{ comment.commenter }}</small>
        <time>{{ getCreated(comment.created_at.seconds) }}</time>
        <p>{{ comment.content }}</p>
        <div class="delete-comment">
          <button
            class="button is-small is-dark"
            @click="onDisplayDeleteComment(comment.id)"
            v-if="(user && (user.id == comment.user_id
            || user.id == '4aRnFohAFYQicirBV3Aa15rkKry2'))"
          >
            Delete
          </button>
        </div>
        <hr />
        <div class="confirmDelete" v-if="displayDeleteComment && selectedComment == comment.id">
          Are you sure you want delete this comment?
          <div class="buttons are-small">
            <button class="button is-danger is-outlined" @click="onDeleteComment(comment.id)">
              Yes
            </button>
            <button class="button is-success is-outlined" @click="displayDeleteComment = false">
              No
            </button>
          </div>
        </div>
      </pre>
    </div>

    <b-notification
      auto-close
      type="is-dark is-outlined"
      v-model="Notify"
      class="position-fixed"
      aria-close-label="Close notification"
    >
     {{notification_msg}}
    </b-notification>
  </section>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex';
// eslint-disable-next-line no-unused-vars
import * as linkify from 'linkifyjs';
import linkifyStr from 'linkifyjs/string';
import xss from 'xss';

export default {
  data: () => ({
    Notify: false,
    notification_msg: '',
    ShowEdit: false,
    confirmCancel: false,
    selectedComment: '',
    displayDeleteConfirm: false,
    displayDeleteComment: false,
    updatedPost: {
      title: '',
      description: '',
      codes: '',
      id: '',
    },
    comment: {
      post_id: '',
      content: '',
    },
  }),
  mounted() {
    this.initUsers();
    this.fullPost(this.$route.params.post_id);
  },
  watch: {
    post() {
      if (this.post[0]) {
        this.initComment(this.post[0].id);
        this.comment.post_id = this.post[0].id;
        document.title = this.post[0].title;
      }
    },
  },
  computed: {
    ...mapGetters({
      usersById: 'users/usersById',
      post: 'comments/post',
    }),
    ...mapState('auth', ['isLoggedIn', 'user']),
    ...mapState('post', ['post', 'votes']),
    ...mapState('comments', ['comments']),
    loadedUsersById() {
      return this.post.reduce((byId, post) => {
        // eslint-disable-next-line no-param-reassign
        byId[post.user_id] = this.usersById[post.user_id]
          ? this.usersById[post.user_id]
          : {
            name: '  Loading...',
            image: 'https://bulma.io/images/placeholders/48x48.png',
          };
        return byId;
      }, {});
    },
  },
  methods: {
    ...mapActions('post', ['fullPost', 'upvote', 'editPost']),
    ...mapActions('blog', ['deletePost', 'IncrementComment', 'decrementComment', 'deleteImage']),
    ...mapActions('users', { initUsers: 'init' }),
    ...mapActions('comments', ['doComment', 'initComment', 'deleteComment']),

    linkExtract(txt) {
      const xss_option = {
        whiteList: {
          a: ['href', 'title', 'target'],
        },
      };
      const option = { defaultProtocol: 'https' };
      return linkifyStr(xss(txt, xss_option), option);
    },
    async onDeletePost(id) {
      if (this.post[0].URL && this.post[0].URL.includes('https://firebasestorage.googleapis.com')) {
        await this.deleteImage(this.post[0]);
      }
      this.displayDeleteConfirm = false;
      this.comments.forEach((comment) => {
        this.deleteComment(comment.id);
      });
      this.deletePost(id);
      this.Notify = true;
      this.notification_msg = 'Post has been deleted';
    },
    async onComment() {
      if (this.comment.content.trim()) {
        this.doComment(this.comment);
        this.comment = {
          content: '',
          post_id: this.post[0].id,
        };
        this.IncrementComment(this.post[0].id);
      }
    },
    onDisplayDeleteComment(id) {
      this.selectedComment = id;
      this.displayDeleteComment = !this.displayDeleteComment;
    },
    async onDeleteComment(id) {
      this.Notify = true;
      try {
        this.displayDeleteComment = !this.displayDeleteComment;
        await this.deleteComment(id);
        this.notification_msg = 'comment has deleted';
        this.decrementComment(this.post[0].id);
      } catch (err) {
        console.log(err);
      }
    },
    async onUpvote() {
      this.upvote(this.post[0].id);
    },
    getCreated(createdAt) {
      function timeSince(date) {
        const seconds = Math.floor((new Date() - date) / 1000);
        let interval = Math.floor(seconds / 31536000);
        if (interval > 1) {
          return `${interval} years`;
        }
        interval = Math.floor(seconds / 2592000);
        if (interval > 1) {
          return `${interval} months`;
        }
        interval = Math.floor(seconds / 86400);
        if (interval > 1) {
          return `${interval} days`;
        }
        interval = Math.floor(seconds / 3600);
        if (interval > 1) {
          return `${interval} hours`;
        }
        interval = Math.floor(seconds / 60);
        if (interval > 1) {
          return `${interval} minutes`;
        }
        return `${Math.floor(seconds || 0)} seconds`;
      }
      return timeSince(createdAt * 1000) < 0
        ? '0 seconds ago'
        : `${timeSince(createdAt * 1000)} ago`;
    },
    clickedEdit() {
      this.ShowEdit = true;
      this.updatedPost.id = this.post[0].id;
      this.updatedPost.title = this.post[0].title;
      this.updatedPost.description = this.post[0].description;
      this.updatedPost.codes = this.post[0].codes;
      this.updatedPost.URL = this.post[0].URL;
    },
    cancelEdit() {
      this.ShowEdit = false;
      this.confirmCancel = false;
    },
    async onEditPost(updatedPost) {
      if (this.post[0].title.trim() !== updatedPost.title.trim()
      || this.post[0].description.trim() !== updatedPost.description.trim()
      || this.post[0].URL.trim() !== updatedPost.URL.trim()
      || this.post[0].codes.trim() !== updatedPost.codes.trim()) {
        this.Notify = true;
        try {
          this.editPost(updatedPost);
          this.notification_msg = 'Post has been updated';
        } catch (err) {
          this.notification_msg = err;
        } finally {
          this.ShowEdit = false;
        }
      } else {
        this.Notify = true;
        this.notification_msg = 'No changes were made';
      }
    },
  },
};
</script>

