import { firestoreAction } from 'vuexfire';
import firebase from '@/firebase';
import db from '@/db';
import storage from '@/storage';

let imageURL;
const posts = db.collection('posts');

const state = {
  blogs: {},
  posts: [],
  upvoted: false,
};

const getters = {
  blog: (state) => (state.blogs[0] ? state.blogs[0] : {}),
};

const actions = {
  async createPost({ getters }, post) {
    const result = posts.doc();
    post.id = result.id;
    post.vote = [];
    post.comment_no = 0;
    post.approval = false;
    post.blog_id = getters.blog.id;
    post.user_id = firebase.auth().currentUser.uid;
    post.created_at = firebase.firestore.FieldValue.serverTimestamp();
    post.updated_at = firebase.firestore.FieldValue.serverTimestamp();

    if (post.file[0]) {
      console.log(post.file[0]);
      await storage.ref('post-image')
        .child(post.user_id)
        .child(post.id)
        .child(post.file[0].name)
        .put(post.file[0])
        .then((res) => res.ref.getDownloadURL())
        .then((url) => {
          post.URL = url;
        });
    }
    delete post.file;

    try {
      await posts.doc(post.id).set(post);
    } catch (error) {
      console.error(error);
    }
  },
  initBlog: firestoreAction(({ bindFirestoreRef }, name) => {
    bindFirestoreRef('blogs', db.collection('blogs').where('name', '==', name));
  }),
  initPost: firestoreAction(({ bindFirestoreRef }, blog_id) => {
    bindFirestoreRef('posts', posts.where('blog_id', '==', blog_id).orderBy('created_at')
      .where('approval', '==', true));
  }),
  async deletePost(_, post_id) {
    await posts.doc(post_id).delete();
  },
  async IncrementComment(_, id) {
    posts.doc(id)
      .update({ comment_no: firebase.firestore.FieldValue.increment(1) });
  },
  async decrementComment(_, id) {
    posts.doc(id)
      .update({ comment_no: firebase.firestore.FieldValue.increment(-1) })
      .catch((error) => {
        console.log(error);
      });
  },
  async deleteImage(_, post) {
    await storage.ref('post-image')
      .child(`${post.user_id}/${post.id}/${post.URL.split('%2F')[3].split('?')[0]}`) // .child(post.URL.split('%2F')[3].split('?')[0])
      .delete().catch((err) => console.log(err));
  },
};

export default {
  namespaced: true,
  state,
  actions,
  getters,
  posts,
  imageURL,
};
