/* eslint-disable no-shadow */
import { firestoreAction } from 'vuexfire';
import firebase from '@/firebase';
import db from '@/db';

const admin = db.collection('admin');
const posts = db.collection('posts');

const state = {
  admin: {},
  posts: [],
};

const actions = {
  initAdmin: firestoreAction(({ bindFirestoreRef }) => {
    let user_id;
    firebase.auth().onAuthStateChanged((user) => {
      if (user) user_id = firebase.auth().currentUser.uid;
      bindFirestoreRef('admin', db.collection('admin').where('user_id', '==', user_id));
    });
  }),
  initPendingPost: firestoreAction(({ bindFirestoreRef }) => {
    bindFirestoreRef('posts', posts.orderBy('created_at')
      .where('approval', '==', false));
  }),
  async approvePost(_, post_id) {
    posts.doc(post_id)
      .update({ approval: true })
      .catch((error) => {
        console.log(error);
      });
  },
};

export default {
  namespaced: true,
  state,
  actions,
  admin,
};
