/* eslint-disable linebreak-style */
/* eslint-disable camelcase */
/* eslint-disable no-unused-vars */
import { firestoreAction } from 'vuexfire';
import firebase from '@/firebase';
import db from '@/db';

const posts = db.collection('posts');

const state = {
  userID: '',
  post: {},
  votes: false,
};

const actions = {
  fullPost: firestoreAction(({ bindFirestoreRef }, id) => {
    bindFirestoreRef('post', posts.where('id', '==', id));
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        state.userID = user.uid;
        posts.doc(id).get()
          .then((doc) => {
            state.votes = doc.data().vote.includes(state.userID);
          });
      }
    });
  }),
  async upvote(_, post_id) {
    if (state.userID != null) {
      posts.doc(post_id).get()
        .then((snapshot) => {
          const document = snapshot.data();
          if (document.vote.includes(state.userID)) {
            posts.doc(post_id)
              .update({ vote: firebase.firestore.FieldValue.arrayRemove(state.userID) });
            state.votes = false;
          } else {
            posts.doc(post_id)
              .update({ vote: firebase.firestore.FieldValue.arrayUnion(state.userID) });
            state.votes = true;
          }
        });
    }
  },
  async editPost(_, post) {
    await posts.doc(post.id).update({
      title: post.title,
      description: post.description,
      codes: post.codes,
      updated_at: firebase.firestore.FieldValue.serverTimestamp(),
    }).then((res) => console.log(res))
      .catch((err) => console.log(err));
  },
};

export default {
  namespaced: true,
  state,
  actions,
};
