/* eslint linebreak-style: ["error", "windows"] */
/* eslint-disable camelcase */
import { firestoreAction } from 'vuexfire';
import firebase from '@/firebase';
import db from '@/db';

const comments = db.collection('comments');

const state = {
  comments: {},
};
const getters = {
  post: (_state) => (_state.post[0] ? _state.post[0] : {}),
};

const actions = {
  // eslint-disable-next-line no-shadow
  async doComment(_, comment) {
    comment.id = comments.doc().id;
    comment.user_id = firebase.auth().currentUser.uid;
    comment.commenter = firebase.auth().currentUser.displayName;
    comment.created_at = firebase.firestore.FieldValue.serverTimestamp();
    comment.updated_at = firebase.firestore.FieldValue.serverTimestamp();
    try {
      await comments.doc(comment.id).set(comment);
    } catch (error) {
      console.error(error);
    }
  },
  initComment: firestoreAction(({ bindFirestoreRef }, post_id) => {
    bindFirestoreRef('comments', comments.where('post_id', '==', post_id)
      .orderBy('created_at'));
  }),
  async deleteComment(_, comment_id) {
    await comments.doc(comment_id).delete();
  },
};

export default {
  namespaced: true,
  state,
  actions,
  getters,
  comments,
};
