import { firestoreAction } from 'vuexfire';
import db from '@/db';

const state = {
  users: [],
};

const users = db.collection('users');

const getters = {
  usersById(_state) {
    return _state.users.reduce((byId, user) => {
      // eslint-disable-next-line no-param-reassign
      byId[user.id] = user;
      return byId;
    }, {});
  },
};
const actions = {
  init: firestoreAction(({ bindFirestoreRef }) => {
    bindFirestoreRef('users', db.collection('users'));
  }),
};

export default {
  namespaced: true,
  state,
  actions,
  getters,
  users,
};
