<script>
// @ is an alias to /src
import { mapActions, mapState } from 'vuex';

let displayState = false;
let displayLogOut = false;
const displayNavbar = () => {
  if (window.innerWidth <= 800) {
    document.querySelector('.nav-container').style.display = displayState ? 'none' : 'block';
    displayState = !displayState;
  }
};
const displayConfirm = () => {
  document.querySelector('.confirmation').style.display = displayLogOut ? 'none' : 'block';
  displayLogOut = !displayLogOut;
};
export default {
  data() {
    return {
      local_storage: localStorage.getItem('isLoggedIn') || 'false',
      BASE_URL: 'https://ccitc.xyz',
    };
  },
  computed: {
    ...mapState('auth', ['user', 'isLoggedIn']),
  },
  methods: {
    ...mapActions('auth', ['login', 'logout']),
    displayNavbar,
    displayConfirm,
  },
};
</script>

